body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100vw;
  overflow: hidden auto;
  background-color: rgb(27, 1, 56);
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.navbar{
    display: flex;
    position: fixed;
    z-index: 100;
    top:0%;
    justify-content: space-between;
    width: 100vw;
    height: 50px;
    background-color: rgb(48 0 92);
}

.navbar ul li{
    list-style: none;
    display: inline-block;
}

.submenu{
    margin: 0 70px;
}

.list{
    text-align: right;
    /* display: inline-block; */
    display: none;
    cursor: pointer;
}

.list li{
    margin-right:25px ;
}

.top-logo{
    width: 200px;
    line-height: 50px;
    margin-left:100px ;
    font-family: 'DynaPuff', cursive;
    font-size: 25px;
}

@media all and (max-width: 930px) {
   .top-logo{
    margin: 0 30px;
    font-size: 15px;
   }
   .list{
    display: none;
   }
}
.home-page{
    margin: auto;
    width: 70vw;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .myimage{
    width: 13vw;
  }
  
  .mydetails{
    line-height: 10px;
  }
  
  .myname{
    color: rgb(0 250 255)
  }
  
  .social-logo{
    margin: 0 10px;
    /* color: rgb(83, 20, 153); */
    color: white;
    font-size: 25px;
  }

  .social-logo:hover{
    color: rgb(106, 106, 251);
  }
  
  .logo ul{
    padding: 0px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .logo ul li{
    margin-top: 0px;
    list-style: none;
    margin-left:0px ;
    margin-right: 10px;
    padding: 0px;
    display: inline;
  }
  .role{
    color: rgb(223, 223, 86);
  }
  .Typewriter{
    display: inline;
  }
  
  .scroll{
    position: absolute;
    padding: 0px;
    bottom: 0px; 
    -webkit-animation: mymove 1s; 
            animation: mymove 1s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }

  .resume{
    margin-top: 30px;
  }

  .resume .myresume{
    all: unset;
    border: 1px solid white;
    padding: 5px;
  }
  .resume .myresume:hover{
    color: black;
    background-color: white;
    cursor: pointer;
    transition: 0.5s;
  }
  .resume .social-logo{
    margin-bottom: -7px;
  }
  
  @-webkit-keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }
  
  @keyframes mymove {
    from {bottom: 0px;}
    to {bottom: 8px;}
    from {bottom: 0px;}
  }

  .scroll ul li{
    list-style: none;
    text-align: center;
  }

  .scroll ul{
    padding: 0px;
  }

  ion-icon {
    pointer-events: none;
   }

   .aboutmetext{
    all: unset;
    cursor: pointer;
   }
  
  @media all and (max-width: 900px) {
    .index{
      flex-direction: column;
    }
    .home-page{
      display: flex;
      flex-direction: column;
      /* justify-content:space-between; */
      justify-content:space-evenly;
      height: 93vh;
      width: 100%;
      align-items: center;
      margin-top: 50px;
      /* margin: 0vh 0px; */
    }

    .scroll{
      /* position: absolute;  */
      position: relative;
      /* padding: 0px; */
      /* bottom: 0%;  */
      -webkit-animation: mymove 1s;
              animation: mymove 1s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
    }

    .myimage{
      width: 150px;
      margin: 0vh 0px;
    }
    .mydetails{
      /* margin-top: 4vh; */
    }
  
  }
.about-page{
    height: 80vh;
}

.about-head{
    margin-top: 150px;
    width: 100vw;
    text-align: center;
}

.about-content{
    display: flex;
    margin: 0 auto;
    width: 80vw;
    height: 60vh;
    align-items: center;
    justify-content: space-evenly;
}

.about-image img{
    display: inline-block;
    border-radius: 20px;
}

.about-intro{
    width: 33vw;
}

.nameRole{
    line-height: 10px;
    margin-bottom: 30px;
}

.age, .phone, .email, .about-name, .about-me    , .contact-me{
    color: rgb(0 250 255);
}

@media all and (max-width: 930px) {
    .about{
        height: 90vh;
        margin: auto;
        display: flex;
        flex-direction: column;
        justify-content:space-evenly;
        /* align-items: flex-end; */
    }
    .about-content{
        flex-direction: column;
        justify-content:space-between;
        /* align-items: center; */
        /* justify-content: center; */
    }

    .about-detail{
        /* color: red; */
        height: 4vh;
    }

    .about-page{
        /* height: 120vh; */
        position: absolute;
        /* top: 80vh; */
    }

    .about-head{
        margin-top: 0px;
        width: 100vw;
        text-align: center;
    }

    .about-intro{
        flex-direction: column;
        width: 90vw;
    }
}
